	module.exports = {"data":{"markdownRemark":{"html":"<p>In this episode, I talk about prototyping tools that I’ve used in the past, and why prototyping is a useful communication practice. If you’re seeking to help people understand a new idea, there’s nothing like prototyping to help you truly help you get at any problems before you jump into the full development work.</p>\n<iframe src=\"https://simplecast.com/e/46156?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>As always, you can get the shownotes on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a> And, hey, please do me a favor and leave a rating and review on iTunes. Check out the <a href=\"http://dearinstructionaldesigner.com/#Review\">DearID website</a> for my information. I’d really appreciate it!</p>","frontmatter":{"date":"October 20, 2016","path":"/blog/dearid-prototyping-tools","title":"#DearID: Prototyping Tools","tags":["design","prototyping","prototyping tools","show your work"],"categories":["Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1,"src":"/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-81d39.png","srcSet":"/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-9a8d1.png 203w,\n/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-c53f9.png 405w,\n/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-81d39.png 810w,\n/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-e460a.png 1215w,\n/static/Prototyping-Tools-9bf23baddd7982c72653b057e2dceac4-f34da.png 1400w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

